import { createContext, useContext, useState, useEffect } from 'react';
import { ethers } from 'ethers';

// Import network configuration from environment variables
const NETWORK_URL = import.meta.env.REACT_APP_NETWORK_URL || 'https://sepolia.infura.io/v3/509fb9fd78ef4236a48802c5f2c78616';
const NETWORK_NAME = import.meta.env.REACT_APP_NETWORK_NAME || 'sepolia';
const CHAIN_ID = parseInt(import.meta.env.REACT_APP_CHAIN_ID || '11155111');
const ACHIEVEMENT_CONTRACT_ADDRESS = import.meta.env.REACT_APP_CONTRACT_ADDRESS_ACHIEVEMENT || '0x5FbDB2315678afecb367f032d93F642f64180aa3';

const WalletContext = createContext();

export const useWallet = () => useContext(WalletContext);

export const WalletProvider = ({ children }) => {
  const [provider, setProvider] = useState(null);
  const [signer, setSigner] = useState(null);
  const [account, setAccount] = useState('');
  const [isConnected, setIsConnected] = useState(false);
  const [chainId, setChainId] = useState(null);
  const [isPortfolioNftOwner, setIsPortfolioNftOwner] = useState(false);

  useEffect(() => {
    // Check if MetaMask is installed
    if (window.ethereum) {
      const ethProvider = new ethers.BrowserProvider(window.ethereum);
      setProvider(ethProvider);

      // Check if already connected
      checkConnection(ethProvider);

      // Listen for account changes
      window.ethereum.on('accountsChanged', (accounts) => {
        if (accounts.length > 0) {
          updateWalletInfo(ethProvider, accounts[0]);
        } else {
          disconnectWallet();
        }
      });

      // Listen for chain changes
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      });
    }

    return () => {
      if (window.ethereum) {
        window.ethereum.removeAllListeners('accountsChanged');
        window.ethereum.removeAllListeners('chainChanged');
      }
    };
  }, []);

  const checkConnection = async (ethProvider) => {
    try {
      const accounts = await window.ethereum.request({ method: 'eth_accounts' });
      if (accounts.length > 0) {
        updateWalletInfo(ethProvider, accounts[0]);
      }
    } catch (error) {
      console.error('Error checking connection:', error);
    }
  };

  const updateWalletInfo = async (ethProvider, address) => {
    setAccount(address);
    setIsConnected(true);
    const ethSigner = await ethProvider.getSigner();
    setSigner(ethSigner);
    const network = await ethProvider.getNetwork();
    setChainId(network.chainId);

    // Check if user owns a portfolio NFT by querying the contract
    try {
      // Import dynamically to avoid circular dependencies
      const { PortfolioNFTService } = await import('../utils/PortfolioNFTService');
      const nftService = new PortfolioNFTService(ethProvider, ethSigner);
      
      // In development, we can use a mock value for testing
      if (import.meta.env.DEV) {
        setIsPortfolioNftOwner(true);
        return;
      }
      
      // In production, actually check balance
      const balance = await nftService.contract.balanceOf(address);
      setIsPortfolioNftOwner(balance > 0);
    } catch (error) {
      console.error("Error checking NFT ownership:", error);
      // Fallback to mock value
      setIsPortfolioNftOwner(Math.random() > 0.5);
    }
  };

  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        // Request account access
        const accounts = await window.ethereum.request({
          method: 'eth_requestAccounts',
        });
        
        // Check if we're on the correct network
        const chainIdHex = await window.ethereum.request({ method: 'eth_chainId' });
        const currentChainId = parseInt(chainIdHex, 16);
        
        if (currentChainId !== CHAIN_ID) {
          try {
            // Try to switch to the correct network
            await window.ethereum.request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: `0x${CHAIN_ID.toString(16)}` }],
            });
          } catch (switchError) {
            // Handle error or user rejection
            console.error('Failed to switch networks:', switchError);
            alert(`Please switch to the ${NETWORK_NAME} network in your wallet.`);
            return;
          }
        }
        
        updateWalletInfo(provider, accounts[0]);
      } catch (error) {
        console.error('User rejected connection:', error);
      }
    } else {
      alert('Please install MetaMask to use this feature!');
    }
  };

  const connectDemoWallet = () => {
    // Simulate connection with a demo wallet
    const demoAddress = '0xf39Fd6e51aad88F6F4ce6aB8827279cffFb92266';
    setAccount(demoAddress);
    setIsConnected(true);
    setChainId(CHAIN_ID);
    setIsPortfolioNftOwner(true); // Demo wallet always owns the NFT
    
    // Create a provider for the demo wallet
    const demoProvider = new ethers.JsonRpcProvider(NETWORK_URL);
    setProvider(demoProvider);
    
    // We don't have a signer for the demo wallet
    setSigner(null);
  };

  const disconnectWallet = () => {
    setAccount('');
    setIsConnected(false);
    setSigner(null);
  };

  return (
    <WalletContext.Provider
      value={{
        provider,
        signer,
        account,
        isConnected,
        chainId,
        isPortfolioNftOwner,
        connectWallet,
        disconnectWallet,
        connectDemoWallet
      }}
    >
      {children}
    </WalletContext.Provider>
  );
};
