import { useState } from 'react';
import { Card, CardBody, Button, Input, Textarea, Checkbox, Select, SelectItem, Divider, useDisclosure, Modal, ModalContent, ModalHeader, ModalBody, ModalFooter } from '@nextui-org/react';
import { useWallet } from '../contexts/WalletContext';
import { useNavigate } from 'react-router-dom';

const CreateChallengePage = () => {
  const navigate = useNavigate();
  const { isConnected, isPortfolioNftOwner, account } = useWallet();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Form state
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    skills: [],
    timeframe: '1',
    complexity: 'medium',
    includeRoadmap: false,
    roadmapSteps: [],
    compensation: '',
  });

  // Predefined skill options
  const skillOptions = [
    { value: 'react', label: 'React' },
    { value: 'javascript', label: 'JavaScript' },
    { value: 'typescript', label: 'TypeScript' },
    { value: 'solidity', label: 'Solidity' },
    { value: 'web3', label: 'Web3.js/Ethers.js' },
    { value: 'nft', label: 'NFT Development' },
    { value: 'defi', label: 'DeFi Protocols' },
    { value: 'node', label: 'Node.js' },
    { value: 'mongodb', label: 'MongoDB' },
    { value: 'graphql', label: 'GraphQL' },
  ];

  // Handle form input changes
  const handleInputChange = (field, value) => {
    setFormData({
      ...formData,
      [field]: value,
    });
  };

  // Handle checkbox for including roadmap
  const handleIncludeRoadmap = (checked) => {
    setFormData({
      ...formData,
      includeRoadmap: checked,
      roadmapSteps: checked ? [
        { description: 'Complete the technical challenge', order: 1 },
        { description: 'Schedule a review call', order: 2 },
      ] : [],
    });
  };

  // Handle roadmap step changes
  const handleRoadmapStepChange = (index, value) => {
    const updatedSteps = [...formData.roadmapSteps];
    updatedSteps[index].description = value;
    setFormData({
      ...formData,
      roadmapSteps: updatedSteps,
    });
  };

  // Add a new roadmap step
  const addRoadmapStep = () => {
    setFormData({
      ...formData,
      roadmapSteps: [
        ...formData.roadmapSteps,
        {
          description: '',
          order: formData.roadmapSteps.length + 1,
        },
      ],
    });
  };

  // Remove a roadmap step
  const removeRoadmapStep = (index) => {
    const updatedSteps = [...formData.roadmapSteps];
    updatedSteps.splice(index, 1);
    // Reorder remaining steps
    updatedSteps.forEach((step, i) => {
      step.order = i + 1;
    });
    setFormData({
      ...formData,
      roadmapSteps: updatedSteps,
    });
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // Simulate form submission
    setTimeout(() => {
      setIsSubmitting(false);
      onOpen(); // Open success modal
    }, 1500);
  };

  // If user is not connected or not an NFT holder, redirect to home
  if (!isConnected || !isPortfolioNftOwner) {
    return (
      <div className="min-h-screen bg-background-dark py-12 px-6 flex items-center justify-center">
        <Card className="bg-background-secondary max-w-md mx-auto">
          <CardBody className="p-8 text-center">
            <div className="w-20 h-20 bg-primary/20 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10 text-primary" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
              </svg>
            </div>
            <h3 className="text-xl font-bold mb-2 text-white">Access Restricted</h3>
            <p className="text-text-secondary mb-6">
              You need to connect your wallet and own a Portfolio NFT to create challenges.
            </p>
            <Button 
              color="primary" 
              onClick={() => navigate('/cv-nft')}
            >
              Get Portfolio NFT
            </Button>
          </CardBody>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background-dark py-12 px-6">
      <div className="container mx-auto max-w-4xl">
        <Card className="bg-background-secondary border-none shadow-md">
          <CardBody className="p-8">
            <h2 className="text-2xl font-bold mb-2 text-white">Create Skill Challenge</h2>
            <p className="text-text-secondary mb-6">
              As a Portfolio NFT holder, you can create custom skill-review challenges for the developer.
            </p>
            
            <form onSubmit={handleSubmit}>
              {/* Challenge Title */}
              <div className="mb-6">
                <Input
                  label="Challenge Title"
                  placeholder="E.g., NFT Integration Challenge"
                  value={formData.title}
                  onChange={(e) => handleInputChange('title', e.target.value)}
                  required
                  className="max-w-full"
                />
              </div>
              
              {/* Challenge Description */}
              <div className="mb-6">
                <Textarea
                  label="Challenge Description"
                  placeholder="Describe the challenge requirements in detail..."
                  value={formData.description}
                  onChange={(e) => handleInputChange('description', e.target.value)}
                  required
                  minRows={3}
                  maxRows={6}
                  className="max-w-full"
                />
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                {/* Required Skills */}
                <div>
                  <Select
                    label="Required Skills"
                    placeholder="Select skills"
                    selectionMode="multiple"
                    value={formData.skills}
                    onChange={(e) => handleInputChange('skills', e.target.value.split(','))}
                    className="max-w-full"
                  >
                    {skillOptions.map((skill) => (
                      <SelectItem key={skill.value} value={skill.value}>
                        {skill.label}
                      </SelectItem>
                    ))}
                  </Select>
                </div>
                
                {/* Complexity */}
                <div>
                  <Select
                    label="Challenge Complexity"
                    placeholder="Select complexity"
                    value={formData.complexity}
                    onChange={(e) => handleInputChange('complexity', e.target.value)}
                    className="max-w-full"
                  >
                    <SelectItem value="easy">Easy</SelectItem>
                    <SelectItem value="medium">Medium</SelectItem>
                    <SelectItem value="hard">Hard</SelectItem>
                  </Select>
                </div>
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                {/* Timeframe */}
                <div>
                  <Select
                    label="Expected Timeframe (days)"
                    placeholder="Select timeframe"
                    value={formData.timeframe}
                    onChange={(e) => handleInputChange('timeframe', e.target.value)}
                    className="max-w-full"
                  >
                    <SelectItem value="1">1 day</SelectItem>
                    <SelectItem value="2">2 days</SelectItem>
                    <SelectItem value="3">3 days</SelectItem>
                    <SelectItem value="5">5 days</SelectItem>
                    <SelectItem value="7">1 week</SelectItem>
                    <SelectItem value="14">2 weeks</SelectItem>
                  </Select>
                </div>
                
                {/* Compensation */}
                <div>
                  <Input
                    label="Compensation (optional)"
                    placeholder="E.g., $500 in ETH"
                    value={formData.compensation}
                    onChange={(e) => handleInputChange('compensation', e.target.value)}
                    className="max-w-full"
                  />
                </div>
              </div>
              
              {/* Roadmap to Getting the Job */}
              <div className="mb-6">
                <Checkbox
                  isSelected={formData.includeRoadmap}
                  onValueChange={handleIncludeRoadmap}
                >
                  <span className="text-white font-medium">Include "Roadmap to Getting the Job"</span>
                </Checkbox>
                
                {formData.includeRoadmap && (
                  <div className="mt-4 ml-7 space-y-4">
                    <p className="text-sm text-text-secondary mb-2">
                      Define the steps that will lead from this challenge to a potential job offer
                    </p>
                    
                    {formData.roadmapSteps.map((step, index) => (
                      <div key={index} className="flex items-start gap-2">
                        <div className="bg-primary/20 rounded-full w-6 h-6 flex-shrink-0 flex items-center justify-center text-xs text-primary font-medium mt-3">
                          {step.order}
                        </div>
                        <div className="flex-grow">
                          <Input
                            label={`Step ${step.order}`}
                            placeholder="Describe this step..."
                            value={step.description}
                            onChange={(e) => handleRoadmapStepChange(index, e.target.value)}
                            className="max-w-full"
                          />
                        </div>
                        <Button
                          color="danger"
                          variant="light"
                          isIconOnly
                          className="mt-2"
                          onPress={() => removeRoadmapStep(index)}
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                          </svg>
                        </Button>
                      </div>
                    ))}
                    
                    <Button
                      color="primary"
                      variant="light"
                      onPress={addRoadmapStep}
                      className="mt-2"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                      </svg>
                      Add Step
                    </Button>
                  </div>
                )}
              </div>
              
              <Divider className="my-6 bg-divider" />
              
              <div className="flex flex-col md:flex-row gap-4 justify-end">
                <Button
                  color="default"
                  variant="bordered"
                  onClick={() => navigate('/')}
                >
                  Cancel
                </Button>
                <Button
                  color="primary"
                  type="submit"
                  isLoading={isSubmitting}
                  className="bg-primary text-black"
                >
                  Create Challenge
                </Button>
              </div>
            </form>
          </CardBody>
        </Card>
      </div>
      
      {/* Success Modal */}
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalContent>
          <ModalHeader className="flex flex-col gap-1">Challenge Created Successfully!</ModalHeader>
          <ModalBody>
            <div className="text-center p-4">
              <div className="w-20 h-20 bg-success/20 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10 text-success" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
              </div>
              <h4 className="text-xl font-bold mb-2">{formData.title}</h4>
              <p className="text-text-secondary mb-4">
                Your challenge has been created and sent to the developer. You'll be notified when they respond.
              </p>
              {formData.includeRoadmap && (
                <div className="bg-background-dark p-4 rounded-lg text-left mb-4">
                  <h5 className="text-white font-medium mb-2">Roadmap to Getting the Job:</h5>
                  <ol className="list-decimal list-inside text-text-secondary space-y-1 pl-2">
                    {formData.roadmapSteps.map((step, index) => (
                      <li key={index}>{step.description}</li>
                    ))}
                  </ol>
                </div>
              )}
              <p className="text-primary font-medium text-sm">
                Challenge ID: CH-{Math.floor(Math.random() * 1000) + 1000}
              </p>
            </div>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" className="w-full" onPress={() => {
              onClose();
              navigate('/');
            }}>
              Return to Dashboard
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default CreateChallengePage;
