import { Outlet, useNavigate, useLocation } from 'react-router-dom';
import { Navbar, NavbarBrand, NavbarContent, NavbarItem, Button, Dropdown, DropdownTrigger, DropdownMenu, DropdownItem, Avatar } from '@nextui-org/react';
import { useWallet } from '../../contexts/WalletContext';

const Layout = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { isConnected, account, connectWallet, disconnectWallet, isPortfolioNftOwner } = useWallet();

  // Function to shorten wallet address for display
  const shortenAddress = (address) => {
    return `${address.slice(0, 6)}...${address.slice(-4)}`;
  };

  const navItems = [
    { name: 'Home', path: '/' },
    { name: 'Games', path: '/games', beta: true },
    { name: 'MarketPlace', path: '/marketplace', beta: true },
    { name: 'My Profile', path: '/cv-nft' },
    { name: 'Skill Demos', path: '/skill-demos' },
    { name: 'Live DApps', path: '/live-dapps' },
    { name: 'Achievements', path: '/achievements' },
  ];

  return (
    <div className="min-h-screen bg-background-dark flex flex-col">
      <Navbar 
        maxWidth="xl" 
        className="bg-background-dark/80 backdrop-blur-md border-b border-divider"
      >
        <NavbarBrand>
          <div 
            className="font-bold text-xl text-inherit cursor-pointer flex items-center" 
            onClick={() => navigate('/')}
          >
            <img 
              src="/images/games-1 2.png" 
              alt="Crypto Oasis" 
              className="h-8 w-8 mr-2" 
            />
            Crypto Oasis
          </div>
        </NavbarBrand>
        
        <NavbarContent className="hidden sm:flex gap-4" justify="center">
          {navItems.map((item) => (
            <NavbarItem 
              key={item.path}
              isActive={location.pathname === item.path}
            >
              <div 
                className={`cursor-pointer px-2 py-1 relative ${
                  location.pathname === item.path 
                    ? 'text-primary font-medium' 
                    : 'text-foreground'
                }`}
                onClick={() => navigate(item.path)}
              >
                {item.name}
                {item.beta && (
                  <span className="absolute -top-1 -right-6 bg-primary/20 text-primary text-xs px-1 rounded">
                    BETA
                  </span>
                )}
              </div>
            </NavbarItem>
          ))}
        </NavbarContent>
        
        <NavbarContent justify="end">
          {isConnected ? (
            <Dropdown placement="bottom-end">
              <DropdownTrigger>
                <Button variant="bordered" className="text-foreground border-foreground">
                  <Avatar
                    size="sm"
                    src={`https://robohash.org/${account}.png`}
                    color="primary"
                    className="mr-2"
                  />
                  {shortenAddress(account)}
                </Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Account actions">
                <DropdownItem key="profile" onClick={() => navigate('/cv-nft')}>
                  My Profile
                </DropdownItem>
                {isPortfolioNftOwner && (
                  <DropdownItem key="create-challenge" onClick={() => navigate('/create-challenge')}>
                    Create Challenge
                  </DropdownItem>
                )}
                <DropdownItem key="disconnect" onClick={disconnectWallet} color="danger">
                  Disconnect
                </DropdownItem>
              </DropdownMenu>
            </Dropdown>
          ) : (
            <Button 
              color="primary" 
              variant="solid"
              onClick={connectWallet}
              className="bg-primary text-black font-medium"
            >
              Connect Wallet
            </Button>
          )}
        </NavbarContent>
      </Navbar>
      
      <main className="flex-grow">
        <Outlet />
      </main>
      
      <footer className="py-10 px-6 bg-background-dark border-t border-divider">
        <div className="container mx-auto">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div>
              <h3 className="text-xl font-bold mb-4 text-white">Crypto Oasis</h3>
              <p className="text-text-secondary">
                An interactive blockchain portfolio showcasing MERN stack and Ethereum development skills.
              </p>
            </div>
            <div>
              <h3 className="text-xl font-bold mb-4 text-white">Quick Links</h3>
              <ul className="space-y-2">
                <li><a href="/" className="text-text-secondary hover:text-primary">Home</a></li>
                <li><a href="/cv-nft" className="text-text-secondary hover:text-primary">My Profile</a></li>
                <li><a href="/skill-demos" className="text-text-secondary hover:text-primary">Skill Demos</a></li>
                <li><a href="/about" className="text-text-secondary hover:text-primary">About</a></li>
              </ul>
            </div>
            <div>
              <h3 className="text-xl font-bold mb-4 text-white">Connect</h3>
              <ul className="space-y-2">
                <li><a href="https://github.com/mar3182" className="text-text-secondary hover:text-primary" target="_blank" rel="noreferrer">GitHub</a></li>
                <li><a href="#" className="text-text-secondary hover:text-primary" target="_blank" rel="noreferrer">LinkedIn</a></li>
                <li><a href="#" className="text-text-secondary hover:text-primary" target="_blank" rel="noreferrer">Twitter</a></li>
              </ul>
            </div>
          </div>
          <div className="mt-8 pt-8 border-t border-divider text-center text-text-secondary">
            <p>&copy; {new Date().getFullYear()} Crypto Oasis Interactive Portfolio. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Layout;
